import React, { useRef } from "react";
import { Helmet } from "react-helmet";

import UnderstandingInklua from "../../components/UnderstandingInklua";
import ModuleOne from "../../components/ModuleOne";
import RetrieveIncoins from "../../components/RetrieveIncoins";
import HowItWorksBanner from "../../components/HowItWorksBanner";
import SquareLinkButton from "../../components/SquareLinkButton";

import FirstModuleImage from "./assets/ilustracao-modulo-1.png";
import FirstModuleImage2x from "./assets/ilustracao-modulo-1@2x.png";
import FirstModuleImage3x from "./assets/ilustracao-modulo-1@3x.png";
import HappyGroupImage from "./assets/grupo-feliz.png";
import HappyGroupImage2x from "./assets/grupo-feliz@2x.png";
import HappyGroupImage3x from "./assets/grupo-feliz@3x.png";
import SecondModuleImage from "./assets/ilustracao-modulo-2.png";
import SecondModuleImage2x from "./assets/ilustracao-modulo-2@2x.png";
import SecondModuleImage3x from "./assets/ilustracao-modulo-2@3x.png";

const HowItWorks = () => {
  const firstBlockRef = useRef();

  const handleScroll = () => {
    const offset = firstBlockRef.current.offsetTop;

    window.scroll({
      top: offset - 80,
      left: 0,
      behavior: "smooth",
    });
  };

  return (
    <>
      <Helmet>
        <title>Inklua</title>
        <link rel="canonical" href={window.location.href} />
        <meta
          property="og:description"
          content="A Inklua atua com inclusão de pessoas com deficiência no mercado de trabalho através dos serviços de recrutamento e seleção, sensibilização e mapeamento"
        ></meta>
        <meta
          property="description"
          content="A Inklua atua com inclusão de pessoas com deficiência no mercado de trabalho através dos serviços de recrutamento e seleção, sensibilização e mapeamento"
        ></meta>
      </Helmet>
      <HowItWorksBanner handleAction={handleScroll} />
      <UnderstandingInklua ref={firstBlockRef} />
      <ModuleOne
        title="Mais do que ouro"
        subtitle="As valiosas <b>Inkoins</b>"
        description="
          <p>Elas são nossas moedas de troca. Após se cadastrar na Inklua, você poderá obtê-las clicando em nossos anúncios ou compartilhando nossas vagas.</p>
          <p>Cada Inkoin resgatada representa um valor em dinheiro real. <strong>Por exemplo: ao resgatar Inkoins de um anúncio, o valor arrecadado pela Inklua com seu clique estará presente nas Inkoins que acabou de resgatar.</strong></p>
        "
        image={[FirstModuleImage, FirstModuleImage2x, FirstModuleImage3x]}
      />
      <ModuleOne
        title="Abradacabra"
        subtitle="A mágica da <br /><b>colaboração</b>"
        description="<p>Após resgatar Inkoins você poderá doá-las para uma causa e é justamente nesse momento em que a mágica acontece: <strong>suas Inkoins se transformam em dinheiro de verdade e são depositadas na caixinha da causa.</strong></p>"
        image={[SecondModuleImage, SecondModuleImage2x, SecondModuleImage3x]}
        inverse
        action={
          <SquareLinkButton
            text="Conheça a causa da vez"
            url="/area-de-doacoes"
          />
        }
      />
      <ModuleOne
        title="Cada ação gera uma reação"
        subtitle="O resultado do <b>sucesso</b>"
        description="<p>Quando tivermos o valor necessário para realizar a causa, faremos uma ou mais postagens dentro da plataforma mostrando o <b>impacto que suas Inkoins proporcionaram na vida das pessoas que você decidiu ajudar.</b></p>"
        image={[HappyGroupImage, HappyGroupImage2x, HappyGroupImage3x]}
      />
      <RetrieveIncoins />
    </>
  );
};

export default HowItWorks;
