import React, { useRef } from "react";
import { Form } from "@unform/web";
import * as Yup from "yup";
import { Link, useHistory } from "react-router-dom";

import SocialAuthentication from "../../../../components/SocialAuthentication";
import { Input } from "../../../../components/NewInput";
import SquareButton from "../../../../components/SquareButton";
import SectionTitle from "../../../../components/SectionTitle";
import { BooleanCheckbox } from "../../../../components/NewInput";
import useAxios from "../../../../hooks/useAxios";
import { useUserContext } from "../../../../contexts/User";
import validationDictionary from "../../../../contants/validationDictionary";

import FieldsGrid from "../../components/FieldsGrid";

const SignUpForm = () => {
  const formRef = useRef(null);
  const { exec, loading } = useAxios();
  const history = useHistory();
  const { updateInkoins, setUserData } = useUserContext();

  const handleSubmit = async (data) => {
    try {
      formRef.current.setErrors({});

      const schema = Yup.object().shape({
        name: Yup.string()
          .min(3, validationDictionary.INVALID_NAME)
          .required(validationDictionary.INVALID_NAME),
        lastname: Yup.string()
          .min(3, validationDictionary.INVALID_LASTNAME)
          .required(validationDictionary.INVALID_LASTNAME),
        email: Yup.string()
          .email(validationDictionary.INVALID_EMAIL)
          .required(validationDictionary.INVALID_EMAIL),
        password: Yup.string()
          .min(6, validationDictionary.PASSWORD_MIN_CHARACTERS)
          .required(validationDictionary.INVALID_PASSWORD),
        password_confirmation: Yup.string().oneOf(
          [Yup.ref("password"), null],
          validationDictionary.PASSWORDS_DO_NOT_MATCH
        ),
        accepted_terms: Yup.bool().oneOf(
          [true],
          validationDictionary.TERMS_ARE_REQUIRED
        ),
      });

      await schema.validate(data, {
        abortEarly: false,
      });

      const response = await exec({
        method: "POST",
        url: "/user/register",
        data,
      });

      if (response.token && response.user && response.user.wallet.coins >= 0) {
        setUserData(response.user);
        updateInkoins(0);
        localStorage.setItem("@user_inkoins", JSON.stringify(0));
        localStorage.setItem("@user_token", response.token);
        localStorage.setItem("@user_data", JSON.stringify(response.user));

        formRef.current.reset();

        history.push("/autenticacao/confirme-seu-email");

        return;
      }

      const errors = response.data.errors;
      const validationErrors = {};

      Object.keys(errors).forEach((err) => {
        validationErrors[err] = errors[err][0];
      });

      formRef.current.setErrors(validationErrors);
    } catch (err) {
      const validationErrors = {};
      if (err instanceof Yup.ValidationError) {
        err.inner.forEach((error) => {
          validationErrors[error.path] = error.message;
        });
        formRef.current.setErrors(validationErrors);
      }
    }
  };

  return (
    <Form ref={formRef} onSubmit={handleSubmit} id="contact-form">
      <SectionTitle
        small
        centered
        level="h2"
        children="Crie sua conta <b>na Inklua</b>"
      />
      <FieldsGrid>
        <Input
          label="Nome"
          name="name"
          placeholder="Digite seu primeiro nome"
          type="text"
        />
        <Input
          label="Sobrenome"
          name="lastname"
          placeholder="Digite seu sobrenome"
          type="text"
        />
        <Input
          label="E-mail"
          name="email"
          placeholder="Digite seu e-mail"
          type="text"
        />
        <Input
          label="Senha"
          name="password"
          placeholder="Digite sua senha"
          type="password"
        />
        <Input
          label="Confirme a senha"
          name="password_confirmation"
          placeholder="Confirme sua senha"
          type="password"
        />
        <BooleanCheckbox name="accepted_terms">
          Li e aceito a{" "}
          <Link target="_blank" to="/politica-de-privacidade">
            Política de Privacidade
            <br /> e Retenção de Dados Pessoais.
          </Link>
        </BooleanCheckbox>
      </FieldsGrid>
      <SquareButton disabled={loading} text="Criar conta" type="submit" />
      <SocialAuthentication inverse />
    </Form>
  );
};

export default SignUpForm;
