import React, { useRef, useEffect, useState } from "react";
import { useField } from "@unform/core";
import { string, func } from "prop-types";

import styles from "./styles.module.scss";

const Radio = ({ name, options, onChange, ...rest }) => {
  const inputRef = useRef([]);
  const { fieldName, defaultValue, registerField } = useField(name);
  const [currentValue, setCurrentValue] = useState(defaultValue);

  useEffect(() => {
    registerField({
      name: fieldName,
      path: "value",
      ref: inputRef.current,
      getValue(refs) {
        const checked = refs.find((ref) => ref.checked);

        return checked ? checked.value : null;
      },
      setValue(refs, value) {
        const item = refs.find((ref) => ref.value === value);

        if (item) {
          item.checked = true;
        }
      },
    });
  }, [fieldName, registerField]);

  return (
    <fieldset className={[styles.wrapper, styles.inverse].join(" ")}>
      {options.map((option, index) => (
        <div key={`field-${name}-${option.id}`}>
          <input
            ref={(elRef) => (inputRef.current[index] = elRef)}
            type="radio"
            name={fieldName}
            value={option.id}
            defaultChecked={defaultValue === option.id}
            id={`field-${name}-${option.id}`}
            className={styles.radio}
            onChange={(e) => {
              setCurrentValue(e.target.value);
              onChange(e.target.value);
            }}
            {...rest}
          />
          <label
            htmlFor={`field-${name}-${option.id}`}
            className={[
              styles.label,
              styles.forRadio,
              currentValue === option.id && styles.active,
            ].join(" ")}
          >
            {option.label}
          </label>
        </div>
      ))}
    </fieldset>
  );
};

Radio.propTypes = {
  name: string.isRequired,
  onChange: func,
};

Radio.propTypes = {
  onChange: () => {},
};

export default Radio;
