import React, { useEffect, useRef } from "react";
import { bool, func } from "prop-types";

import styles from "./styles.module.scss";

const Tooltip = ({ visible, handleClose }) => {
  const ref = useRef();

  useEffect(() => {
    if (visible) {
      ref.current.focus();
    }
  }, [visible]);

  return (
    <div
      className={[styles.wrapper, visible ? styles.visible : null].join(" ")}
      tabIndex="0"
      ref={ref}
    >
      <p className={styles.paragraph}>
        Bem-vindo! Agora você já pode acumular <b>Inkoins</b> lendo e
        compartilhando conteúdos ou vagas.
      </p>
      <button
        onClick={handleClose}
        className={styles.button}
        aria-label="Entendi, fechar dica"
      >
        Entendi
      </button>
    </div>
  );
};

Tooltip.propTypes = {
  visible: bool.isRequired,
  handleClose: func.isRequired,
};

export default Tooltip;
