import React from "react";
import { oneOf, bool, shape } from "prop-types";
import { Route } from "react-router-dom";

import InstitutionalHeader from "../InstitutionalHeader";
import InstitutionalFooter from "../InstitutionalFooter";
import AlternativeHeader from "../AlternativeHeader";
import Header from "../Header";
import Footer from "../Footer";

const CustomRoute = ({ component: Component, options, ...rest }) => {
  const { hideHeader, hideFooter, headerVersion, footerVersion } = options;

  const renderHeader = () => {
    if (hideHeader) return;

    if (headerVersion === "simple") {
      return <AlternativeHeader />;
    }

    if (headerVersion === "institutional") {
      return <InstitutionalHeader />;
    }

    return <Header />;
  };
  const renderFooter = () => {
    if (hideFooter) return;

    if (footerVersion === "institutional") {
      return <InstitutionalFooter />;
    }

    return <Footer />;
  };

  return (
    <>
      {renderHeader()}
      <Route {...rest}>
        <Component />
      </Route>
      {renderFooter()}
    </>
  );
};

CustomRoute.propTypes = {
  options: shape({
    hideHeader: bool,
    headerVersion: oneOf(["default", "institutional", "simple"]),
    hideFooter: bool,
    footerVersion: oneOf(["default", "institutional"]),
  }),
};

CustomRoute.defaultProps = {
  options: {
    hideHeader: false,
    headerVersion: "default",
    hideFooter: false,
    footerVersion: "default",
  },
};

export default CustomRoute;
