import React from "react";
import { oneOf, node, string, bool } from "prop-types";

import styles from "./styles.module.scss";

const SectionTitle = ({
  children,
  level,
  color,
  linebreak,
  small,
  centered,
}) => {
  const renderContent = () => ({
    __html: children,
  });

  if (level === "h2") {
    return (
      <h2
        className={[
          styles.title,
          centered && styles.centered,
          small && styles.small,
          linebreak && styles.linebreak,
        ].join(" ")}
        style={{ color: color }}
        dangerouslySetInnerHTML={renderContent()}
      />
    );
  }

  if (level === "span") {
    return (
      <span
        className={[
          styles.title,
          centered && styles.centered,
          small && styles.small,
          linebreak && styles.linebreak,
        ].join(" ")}
        style={{ color: color }}
        dangerouslySetInnerHTML={renderContent()}
      />
    );
  }

  return (
    <p
      className={[
        styles.title,
        centered && styles.centered,
        small && styles.small,
        linebreak && styles.linebreak,
      ].join(" ")}
      style={{ color: color }}
      dangerouslySetInnerHTML={renderContent()}
    />
  );
};

SectionTitle.propTypes = {
  children: node.isRequired,
  level: oneOf(["h2", "span", "p"]).isRequired,
  color: string,
  linebreak: bool,
  small: bool,
  centered: bool,
};

SectionTitle.defaultProps = {
  color: null,
  linebreak: true,
  small: false,
  centered: false,
};

export default SectionTitle;
