import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

import { User } from "../../libs/icons";
import { lockScroll } from "../../helpers";
import IconLink from "../../components/IconLink";
import SearchField from "../../components/SearchField";
import Menu from "../../components/Menu";
import MenuTrigger from "../../components/MenuTrigger";
import InkoinCounter from "../../components/InkoinCounter";
import Tooltip from "../../components/Tooltip";
import useUserData from "../../hooks/useUserData";

import Logo from "./logo-inklua-color.svg";
import styles from "./styles.module.scss";

const Header = () => {
  const [isFloating, setFloating] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [tooltipVisibility, setTooltipVisibility] = useState(false);
  const { isRegistrationComplete } = useUserData();

  const handleScroll = () => {
    setFloating(window.scrollY > 0);
  };

  const closeTooltip = () => {
    const userData = localStorage.getItem("@user_data");
    const { email } = JSON.parse(userData);
    localStorage.setItem(`@user_viewed_tooltip_${email}`, true);

    setTooltipVisibility(false);
  };

  useEffect(() => {
    lockScroll(isMenuOpen);
  }, [isMenuOpen]);

  useEffect(() => {
    const userData = localStorage.getItem("@user_data");
    if (userData) {
      const { email } = JSON.parse(userData);
      if (localStorage.getItem(`@user_viewed_tooltip_${email}`)) {
        return;
      }

      setTooltipVisibility(true);
    }
  }, []);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <>
      <span className={styles.spacer} aria-hidden="true" />
      <header
        className={[styles.wrapper, isFloating && styles.floating].join(" ")}
      >
        <div className={styles.container}>
          <Link to="/" aria-label="Início">
            <img src={Logo} alt="Inklua" />
          </Link>
          <SearchField />
          <div className={styles.links}>
            {localStorage.getItem("@user_token") && (
              <>
                {isRegistrationComplete() && tooltipVisibility && (
                  <Tooltip
                    visible={tooltipVisibility}
                    handleClose={closeTooltip}
                  />
                )}
                <InkoinCounter />
              </>
            )}
            {!localStorage.getItem("@user_token") && (
              <IconLink to="/autenticacao/login" text="Entrar" type="link">
                <User color="#1B9B9E" hidden />
              </IconLink>
            )}
            <MenuTrigger onClick={() => setIsMenuOpen(true)} />
          </div>
        </div>
      </header>
      <Menu visible={isMenuOpen} handleClose={() => setIsMenuOpen(false)} />
    </>
  );
};

Header.propTypes = {};

export default Header;
