import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

import useAxios from "../../../../hooks/useAxios";

import AuthenticationWrapper from "../../components/AuthenticationWrapper";
import EmailValidatedSuccesfully from "../../components/EmailValidatedSuccesfully";
import InvalidConfirmation from "../../components/InvalidConfirmation";

const ConfirmYourEmail = () => {
  const [validationStatus, setValidationStatus] = useState(false);
  const { search } = useLocation();
  const params = new URLSearchParams(search);
  const { exec, loading } = useAxios();

  useEffect(() => {
    const values = {
      key: params.get("key"),
      hash: params.get("hash"),
      expires: params.get("expires"),
      signature: params.get("signature"),
    };

    const validateEmail = async () => {
      try {
        const response = await exec({
          method: "GET",
          url: `/email/verify/${values.key}/${values.hash}?expires=${values.expires}&signature=${values.signature}`,
          useCredentials: true,
        });

        if (response.message === "E-mail confirmado") {
          setValidationStatus("valid");
          const userData = JSON.parse(localStorage.getItem("@user_data"));
          localStorage.setItem(
            "@user_data",
            JSON.stringify({
              ...userData,
              email_verified_at: true,
            })
          );
        }
      } catch (error) {
        console.log({ error });
      }
    };

    validateEmail();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!validationStatus || loading) {
    return null;
  }

  return (
    <AuthenticationWrapper>
      {validationStatus === "invalid" ? (
        <InvalidConfirmation />
      ) : (
        <EmailValidatedSuccesfully />
      )}
    </AuthenticationWrapper>
  );
};

export default ConfirmYourEmail;
