import React, { useRef } from "react";
import { Helmet } from "react-helmet";

import StampMarquee from "../../components/StampMarquee";
import SectionContactForm from "../../components/SectionContactForm";
import WhereWeAre from "../../components/WhereWeAre";
import TestimonialsSection from "../../components/TestimonialsSection";
import AccordionList from "../../components/AccordionList";
import ModuleOne from "../../components/ModuleOne";
import IntroductionPanel from "../../components/IntroductionPanel";

import WomanInWheelChair1x from "./assets/mulher-cadeira-de-roda.png";
import WomanInWheelChair2x from "./assets/mulher-cadeira-de-roda@2x.png";
import WomanInWheelChair3x from "./assets/mulher-cadeira-de-roda@3x.png";
import DiversityImage from "./assets/shutterstock_1776687221.jpg";

const testimonials = [
  {
    id: 1,
    quote:
      "Meu contato foi na solicitação e no repasse dos candidatos triados pela Inklua. No processo das entrevistas com a área, o qual tive a oportunidade de acompanhar, tivemos excelentes candidatos tendo em vista os requisitos. Tínhamos 3 finalistas diversos com boas condições de execução de acordo com a necessidade. Foi uma experiência muito gratificante encontrar e poder conversar com os profissionais!!!",
    name: "Camila",
    description: "Indústria de papel e celulose",
    avatar: null,
  },
  {
    id: 2,
    quote:
      "A Inklua vem nos apoiando de maneira fundamental! Apresenta muita agilidade no retorno, apresentação do material de forma organizada e sempre dentro do prazo e uma atenção especial junto aos candidatos.\nSeu suporte em identificar profissionais com competência alinhada à nossa necessidade está sendo fundamental para fortalecermos a cultura de inclusão dentro da empresa!",
    name: "Julia",
    description: "Indústria siderúrgica",
    avatar: null,
  },
  {
    id: 3,
    quote:
      "A parceria com a Inklua tem sido uma jornada de apoio mútuo e alcance de objetivos. Todos sabemos das dificuldades de contratar com qualidade e assertividade. A Inklua é uma empresa com altíssima qualidade, excelente atendimento e focada na solução. Juntos fortalecemos a nossa marca empregadora e apoiamos iniciativas voltadas para inclusão de pessoas com deficiência.",
    name: "Ana",
    description: "Varejo de construção",
    avatar: null,
  },
  {
    id: 4,
    quote:
      "Fiquei impressionada com a eficácia, a rapidez e a excelência no atendimento...fui indicada a uma vaga de emprego em uma das melhores e mais conceituadas empresas. Estou muito feliz e realizada e tenho muito a agradecer ao atendimento da colaboradora Priscila a qual fez todo o procedimento com respeito, atenção e carinho a minha pessoa.",
    name: "Aline de Fátima",
    description: "Candidato",
    avatar: null,
  },
  {
    id: 5,
    quote:
      "Empresa de excelente calibre! Fico muito feliz que, no mercado de trabalho atual, existam profissionais que dão oportunidades às pessoas com condições especiais.\nConversei com uma excelente profissional que tirou as minhas dúvidas e me esclareceu sobre a oportunidade. Continue com o primoroso trabalho Karoline, você é um exemplo de inclusão e oportunidade, junto com toda a sua equipe.",
    name: "Taires Amanda",
    description: "Candidato",
    avatar: null,
  },
  {
    id: 6,
    quote:
      "A Inklua é excelente e eficiente no que faz, tem diversas vagas entre diversos cargos, as vagas são todas voltadas para PcDs. A atendente Macielly é super atenciosa e carinhosa, fez meu acompanhamento no processo seletivo desde o começo e ofereceu o melhor atendimento possível, só tenho a agradecer. Muito obrigada!",
    name: "Gabriela Ferreira",
    description: "Candidato",
    avatar: null,
  },
];

const ForCompanies = () => {
  const fistBlockRef = useRef();
  const formBlockRef = useRef();

  const handlePanelScroll = () => {
    fistBlockRef.current.scrollIntoView({ behavior: "smooth" });
  };

  const handleFormScroll = () => {
    formBlockRef.current.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <>
      <Helmet>
        <title>Inklua</title>
        <link rel="canonical" href={window.location.href} />
        <meta
          property="og:description"
          content="A Inklua atua com inclusão de pessoas com deficiência no mercado de trabalho através dos serviços de recrutamento e seleção, sensibilização e mapeamento"
        ></meta>
        <meta
          property="description"
          content="A Inklua atua com inclusão de pessoas com deficiência no mercado de trabalho através dos serviços de recrutamento e seleção, sensibilização e mapeamento"
        ></meta>
      </Helmet>
      <IntroductionPanel
        title="Pensando em uma sociedade mais inclusiva"
        description="Atuamos pela equidade das pessoas com deficiência dentro das empresas."
        primaryButtonText="Vamos conversar?"
        handlePrimaryAction={handleFormScroll}
        handleSecondaryAction={handlePanelScroll}
        image={DiversityImage}
      />
      <ModuleOne
        ref={fistBlockRef}
        title="Somos Inklua"
        subtitle="Inclusão no mercado <b>de trabalho</b>"
        description="<p>Trabalhamos para viabilizar a inclusão nas empresas, através da valorização do indivíduo e da diversidade. Promovemos conexões assertivas entre profissionais e oportunidades.</p>"
        image={[WomanInWheelChair1x, WomanInWheelChair2x, WomanInWheelChair3x]}
      />
      <AccordionList />
      <TestimonialsSection data={testimonials} />
      <StampMarquee />
      <WhereWeAre
        locations={[
          "São Paulo/SP",
          "Rio de Janeiro/RJ",
          "Belo Horizonte/MG",
          "Jundiaí/SP",
          "São Bernardo do Campo/SP",
        ]}
      />
      <SectionContactForm type="simple" ref={formBlockRef} />
    </>
  );
};

export default ForCompanies;
