import React, { useState } from "react";

import { Magnifier, Plus } from "../../libs/icons";

import styles from "./styles.module.scss";

const SearchField = () => {
  const isMobile = window.innerWidth <= 440 ? false : true;
  const [open, setOpen] = useState(isMobile);
  const [term, setTerm] = useState("");

  const toggleSearch = () => {
    if (isMobile) {
      return;
    }
    setOpen(!open);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (term === null || term.trim() === "") return;
    window.location.href = `/busca?termo=${term}`;
  };

  const handleChange = (event) => {
    setTerm(event.target.value);
    event.preventDefault();
  };

  return (
    <div className={[styles.wrapper, !open && styles.collapsed].join(" ")}>
      <form onSubmit={handleSubmit} aria-label="Busca" role="search">
        <button
          className={styles.button}
          type={isMobile && open ? "submit" : "button"}
          onClick={toggleSearch}
          aria-label={isMobile && open ? "Buscar" : "Abrir busca"}
        >
          <Magnifier color="#1B9B9E" />
        </button>
        <input
          onChange={handleChange}
          className={[styles.field, !open && styles.collapsed].join(" ")}
          type="text"
          aria-label="Procure por cargo ou palavra-chave"
          placeholder="Procure por cargo ou palavra-chave"
          name="busca"
          aria-hidden={!isMobile && !open}
          aria-required="true"
        />
        <button
          className={[
            styles.button,
            styles.closeButton,
            !open && styles.closed,
          ].join(" ")}
          type="button"
          onClick={toggleSearch}
          aria-label="Fechar"
        >
          <Plus />
        </button>
      </form>
    </div>
  );
};

export default SearchField;
