import React from "react";
import { func, string } from "prop-types";

import ScrollButton from "../../components/ScrollButton";

import styles from "./styles.module.scss";

const IntroductionPanel = ({
  title,
  description,
  primaryButtonText,
  handlePrimaryAction,
  handleSecondaryAction,
  image,
}) => {
  return (
    <section
      className={styles.wrapper}
      style={{ backgroundImage: `url(${image})` }}
    >
      <div className={styles.content}>
        <p className={styles.title}>{title}</p>
        <p className={styles.description}>{description}</p>
        {primaryButtonText && handlePrimaryAction && (
          <button className={styles.button} onClick={handlePrimaryAction}>
            {primaryButtonText}
          </button>
        )}
      </div>
      {handleSecondaryAction && (
        <ScrollButton
          handleClick={handleSecondaryAction}
          defaultColor="#1b9b9e"
          hoverColor="#1b9b9e"
        />
      )}
    </section>
  );
};

IntroductionPanel.propTypes = {
  title: string.isRequired,
  description: string.isRequired,
  primaryButtonText: string,
  handlePrimaryAction: func,
  handleSecondaryAction: func,
};

IntroductionPanel.defaultProps = {
  primaryButtonText: null,
  handlePrimaryAction: () => {},
  handleSecondaryAction: () => {},
};

export default IntroductionPanel;
