import React from "react";
import { Link } from "react-router-dom";

import Config from "../../config";
import SocialMediaLink from "../SocialMediaLink";
import { Instagram, Facebook, LinkedIn } from "../../libs/icons";

import Logo from "./logo-inklua-white.svg";
import styles from "./styles.module.scss";

const Footer = () => {
  const renderUserButton = () => {
    let path = "/autenticacao/login";

    if (localStorage.getItem("@user_token")) {
      path = "/minha-conta";
    }

    return (
      <li>
        <Link to={path}>Minha Conta</Link>
      </li>
    );
  };

  return (
    <footer className={styles.wrapper}>
      <div className={styles.container}>
        <div className={styles.firstColumn}>
          <Link to="/">
            <img src={Logo} alt="Inklua" className={styles.logo} />
          </Link>
          <p className={styles.credits}>
            Inklua.com.br © 2021
            <br />
            Todos os direitos reservados
          </p>
        </div>
        <div className={styles.secondColumn}>
          <ul className={styles.menu}>
            <li>
              <Link to="/area-de-doacoes">Área de doações</Link>
            </li>
            <li>
              <Link to="/como-funciona">Como funciona</Link>
            </li>
            {renderUserButton()}
            <li>
              <Link to="/contato">Contato</Link>
            </li>
            <li>
              <a href="https://inklua.compleo.com.br/Curriculo">Enviar Currículo</a>
            </li>
            <li>
              <Link to="/para-empresas" rel="noreferrer" target="_blank">
                Para empresas
              </Link>
            </li>

          </ul>
        </div>
        <div>
          <div className={styles.sociaMediaWrapper}>
            <SocialMediaLink url={Config.INSTAGRAM_URL}>
              <Instagram color="#fff" />
            </SocialMediaLink>
            <SocialMediaLink url={Config.FACEBOOK_URL}>
              <Facebook color="#fff" />
            </SocialMediaLink>
            <SocialMediaLink url={Config.LINKEDIN_URL}>
              <LinkedIn color="#fff" />
            </SocialMediaLink>
          </div>
          <p className={styles.links}>
            <Link to="/politica-de-privacidade">
              Politica de Privacidade
              <br /> &amp; Termos de Uso
            </Link>
          </p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
