import React, { useState } from "react";
import { string, bool, number } from "prop-types";

import { Inkoin } from "../../libs/icons";
import Config from "../../config";
import useAxios from "../../hooks/useAxios";
import { useUserContext } from "../../contexts/User";

import InkoinCheck from "./inkoin-check.png";
import styles from "./styles.module.scss";

const ButtonWithInkoinSupport = ({ text, url, hasInkoins, id }) => {
  const [clicked, setClicked] = useState(!hasInkoins);
  const [showFeedback, setShowFeedback] = useState(false);
  const { loading, exec } = useAxios();
  const { addInkoins } = useUserContext();

  const handleClick = async () => {
    if (clicked) return;

    setClicked(true);

    const response = await exec({
      useCredentials: true,
      method: "POST",
      url: "/actions/share",
      data: {
        content_id: id,
        action: "click",
        media: "ad",
      },
    });

    if (response?.data?.coins) {
      setShowFeedback(true);
      addInkoins(response.data.coins);
    }

    setTimeout(() => {
      setShowFeedback(false);
    }, Config.INKOIN_ANIMATION_INTERVAL * 4);
  };

  if (loading) {
    return (
      <span className={styles.fakeButton} data-testid="fake-button">
        <Inkoin /> Resgatando…
      </span>
    );
  }

  if (showFeedback) {
    return (
      <a
        href={url}
        className={styles.url}
        rel="noreferrer"
        target="_blank"
        onClick={handleClick}
        data-testid="anchor"
      >
        <img
          className={styles.icon}
          src={InkoinCheck}
          alt="Resgatamos suas"
          aria-hidden="true"
          data-testid="icon"
        />
        {Config.INKOIN_AD_CLICK_VALUE} Inkoins
      </a>
    );
  }

  return (
    <a
      href={url}
      className={styles.url}
      rel="noreferrer"
      target="_blank"
      onClick={handleClick}
      data-testid="anchor"
    >
      {text}
      {!clicked && (
        <span className={styles.inkoinWrapper}>
          <Inkoin /> {Config.INKOIN_AD_CLICK_VALUE}
        </span>
      )}
    </a>
  );
};

ButtonWithInkoinSupport.propTypes = {
  text: string.isRequired,
  url: string.isRequired,
  hasInkoins: bool.isRequired,
  id: number.isRequired,
};

export default ButtonWithInkoinSupport;
