import React from "react";
import Slick from "react-slick";

import HeroBanner from "../../components/HeroBanner";
import SliderBannerA2x from "../../assets/images/home-slider-01@2x.png";
import SliderBannerB2x from "../../assets/images/home-slider-02@2x.png";

import styles from "./styles.module.css";
import "./styles.css";

const sliderData = [
  {
    id: 2,
    title: "Resgate Inkoins para ajudar <b>quem precisa<b>",
    description:
      "Acesse os conteúdos da Inklua e compartilhe nossas vagas para resgatar Inkoins e doá-las para uma causa.",
    image: SliderBannerB2x,
    link: {
      url: "/como-funciona",
      text: "Saiba como funciona",
    },
  },
  {
    id: 1,
    title: "Se <b>#inklua</b> no mercado de trabalho",
    description:
      "Somos uma plataforma feita para que você explore vagas, conteúdos e se #inklua conosco.",
    image: SliderBannerA2x,
    link: null,
  },
];

const HeroSlider = () => {
  const settings = {
    dots: true,
    infinite: false,
    slidesToShow: 1,
    speed: 500,
    arrows: false,
    autoPlay: true,
    autoPlaySpeed: 6000,
    adaptiveHeight: true,
  };

  return (
    <div className={styles.wrapper}>
      <Slick {...settings}>
        {sliderData.map((item) => (
          <HeroBanner
            id={item.id}
            key={`banner_${item.id}`}
            title={item.title}
            description={item.description}
            image={item.image}
            link={item.link}
          />
        ))}
      </Slick>
    </div>
  );
};

export default HeroSlider;
