import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { Helmet } from "react-helmet";

import PositionDescription from "../../components/PositionDescription";
import useAxios from "../../hooks/useAxios";
import { toDate, stripHTML } from "../../helpers";

const Position = () => {
  const { id } = useParams();
  const [contentData, setContentData] = useState(null);
  const [isLoading, setLoading] = useState(true);
  const { exec, loading } = useAxios();

  const handleCompleoUrl = (url) => {
    return url.replace("/Visualizar/", "/PreCandidatar/");
  };

  const fetchData = async () => {
    const response = await exec({
      useCredentials: true,
      method: "get",
      url: `/vaga/${id}`,
    });

    setContentData(response);
    setLoading(false);
  };

  const parseDescriptionToMetatags = (string) => {
    return stripHTML(string).slice(0, 150);
  };

  useEffect(() => {
    fetchData();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (isLoading || loading) {
    return null;
  }

  return (
    <>
      <Helmet>
        <title>
          Inklua | Vaga para PcD: {contentData.title} – {contentData.location}
        </title>
        <link rel="canonical" href={window.location.href} />
        <meta
          property="og:description"
          content={parseDescriptionToMetatags(contentData.description)}
        ></meta>
        <meta
          property="description"
          content={parseDescriptionToMetatags(contentData.description)}
        ></meta>
      </Helmet>
      <PositionDescription
        title={contentData.title}
        date={toDate(contentData.date, "DD/MM/YYYY")}
        text={contentData.description}
        url={handleCompleoUrl(contentData.url)}
        id={Number(id)}
        shares={contentData.shares}
      />
    </>
  );
};

export default Position;
