import React, { useRef, useEffect } from "react";
import { bool, string, node } from "prop-types";
import { useField } from "@unform/core";

import styles from "./styles.module.scss";

const BooleanCheckbox = ({ name, children, checked }) => {
  const inputRef = useRef();
  const { fieldName, registerField, error, clearError } = useField(name);

  useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef,
      getValue: (ref) => ref.current.checked,
      setValue: (ref, value) => {
        ref.current.value = ref.current.checked;
      },
      clearValue: (ref) => {
        ref.current.value = false;
      },
    });
  }, [fieldName, registerField]);

  return (
    <div className={styles.wrapper}>
      {error && (
        <span className={styles.errorMessage} id={`error_${name}`}>
          {error}
        </span>
      )}
      <input
        ref={inputRef}
        defaultChecked={checked}
        value={name}
        type="checkbox"
        id={`input-${name}`}
        className={styles.input}
        onChange={clearError}
        aria-describedby={`error_${name}`}
      />
      <label
        htmlFor={`input-${name}`}
        className={[styles.label, error && styles.invalid].join(" ")}
      >
        {children && <span className={styles.falseLabel}>{children}</span>}
      </label>
    </div>
  );
};

BooleanCheckbox.propTypes = {
  name: string.isRequired,
  label: node,
  checked: bool,
};

BooleanCheckbox.defaultProps = {
  label: null,
  checked: false,
};

export default BooleanCheckbox;
