import React from "react";
import { arrayOf, string, func, shape, number } from "prop-types";

import Icon from "./dropdown-icon.svg";
import styles from "./styles.module.scss";

const SearchFilter = ({ options, onChange, selected }) => (
  <div className={styles.wrapper}>
    <span className={styles.label}>Filtre as vagas por cidade</span>
    {options.length && (
      <select
        onChange={onChange}
        className={styles.select}
        style={{ backgroundImage: `url(${Icon})` }}
        defaultValue={selected || ""}
      >
        <option value="" disabled hidden>
          Selecione uma cidade
        </option>
        {selected && <option value="Todas">Todas</option>}
        {options.map((option) => (
          <option
            key={`option_${option.value}_${option.quantity}`}
            value={option.value}
          >
            {option.name} ({option.quantity})
          </option>
        ))}
      </select>
    )}
  </div>
);

SearchFilter.propTypes = {
  onChange: func.isRequired,
  selected: string,
  options: arrayOf(
    shape({
      name: string.isRequired,
      value: string.isRequired,
      quantity: number.isRequired,
    })
  ).isRequired,
};

SearchFilter.defaultProps = {
  selected: null,
};

export default SearchFilter;
