import React from "react";

import styles from "./styles.module.scss";

const PrivacyPolicyContent = () => (
  <main className={styles.wrapper}>
    <small className={styles.preffix}>
      A política de privacidade inicia vigência em 2 de abril de 2021
    </small>
    <h1>Política de privacidade e retenção de dados pessoais</h1>
    <p>
      Os dados não são acessados por terceiros e não comercializamos dados de
      nenhum usuário.
    </p>
    <p>A não aceitação deste termo impossibilita a utilização da plataforma.</p>
    <p>
      Para cancelar o aceite em relação a utilização de seus dados, o usuário
      deverá excluir sua conta da plataforma.
    </p>
    <h2>Utilização de dados pessoais:</h2>
    <p>
      Nome e endereço de e-mail são coletados caso o login seja feito com alguma
      rede social. A utilização destes dados é somente para comunicação, ao
      enviar e-mails e personalizar seu usuário.
    </p>
    <h2>Dados de navegação e dos dispositivos:</h2>
    <p>
      Para realizar análises, personalizar e desenvolver novas melhorias e
      funcionalidades realizamos o armazenamento dos dados de sua navegação em
      nossa plataforma, bem como os identificadores únicos de e modelos dos seus
      dispositivos.
    </p>
    <h2>Direito de retificação:</h2>
    <p>
      Ao acessar seu perfil o usuário pode editar os dados, se função para a
      edição não estiver disponível no meio acessado a alteração de dados pode
      ser solicitada mediante contato no chat, com prazo de conclusão em até 10
      dias úteis.
    </p>
    <h2>Direito ao esquecimento:</h2>
    <p>
      O usuário pode excluir sua conta ao acessar seu perfil, se a função para a
      exclusão do usuário não estiver disponível no meio acessado a exclusão da
      conta pode ser solicitada mediante contato pelo e-mail
      contato@inklua.com.br, com prazo de conclusão em até 10 dias úteis. Ao
      excluir a conta os dados pessoais são anonimizados em nosso banco de
      dados, os registros referentes a doações serão preservados, porém não é
      possível identificar o usuário que realizou as doações e tampouco
      recuperar esses registros.
    </p>
    <h2>Direito a portabilidade:</h2>
    <p>
      Para receber seus dados pessoais e registros de doações efetuadas deve-se
      solicitar o envio dos mesmos pelo e-mail contato@inklua.com.br, com prazo
      de conclusão em até 10 dias úteis. O envio será feito em arquivo no
      formato JSON.
    </p>
  </main>
);

export default PrivacyPolicyContent;
