import React from "react";
import { string, oneOf, bool } from "prop-types";
import { Link } from "react-router-dom";

import styles from "./styles.module.scss";

const SquareLinkButton = ({ text, url, size, type, inline, rel }) => {
  if (rel === "external" || rel === "noreferrer") {
    return (
      <a
        href={url}
        className={[
          styles.url,
          size === "small" ? styles.small : styles.medium,
          type === "fill" ? styles.fill : styles.outline,
          inline && styles.inline,
        ].join(" ")}
        rel="noreferrer"
        target="_blank"
      >
        {text}
      </a>
    );
  }

  return (
    <Link
      to={url}
      className={[
        styles.url,
        size === "small" ? styles.small : styles.medium,
        type === "fill" ? styles.fill : styles.outline,
        inline && styles.inline,
      ].join(" ")}
      rel={rel}
    >
      {text}
    </Link>
  );
};

SquareLinkButton.propTypes = {
  text: string.isRequired,
  url: string.isRequired,
  size: oneOf(["small", "medium"]),
  type: oneOf(["fill", "outline"]),
  rel: oneOf(["external", "next", "noreferrer"]),
  inline: bool,
};

SquareLinkButton.defaultProps = {
  size: "medium",
  type: "fill",
  inline: false,
  rel: "next",
};

export default SquareLinkButton;
