import React from "react";
import { string, number } from "prop-types";
import { useHistory } from "react-router-dom";

import ShareButton from "../../components/ShareButton";
import SquareLinkButton from "../../components/SquareLinkButton";
import { LeftArrow } from "../../libs/icons";

import styles from "./styles.module.scss";

const PositionDescription = ({ title, date, text, url, id, shares }) => {
  const history = useHistory();

  const renderContent = () => ({
    __html: text,
  });

  const handleGoBack = () => {
    history.goBack();
  };

  return (
    <>
      <main>
        <div className={styles.header}>
          <button onClick={handleGoBack} className={styles.backButton}>
            <LeftArrow />
          </button>
          <div className={styles.detail}>Detalhe da vaga</div>
          <ShareButton
            text="Compartilhar"
            title={title}
            url={window.location.href}
            id={id}
            shares={shares}
          />
        </div>
        <div className={styles.wrapper}>
          <div>
            <p className={styles.profileWarning}>
              <strong>Vaga exclusiva para pessoas com deficiência</strong>
            </p>
            <h1>{title}</h1>
            <p className={styles.date}>
              Publicada em: <b>{date}</b>
            </p>
          </div>
          <div
            className={styles.descriptionWrapper}
            dangerouslySetInnerHTML={renderContent()}
          />
          <div className={styles.footer}>
            <p>
              Ao clicar no botão abaixo você concorda com os{" "}
              <a
                href="https://ats.compleo.com.br/TermosServicoCandidato"
                target="_blank"
                rel="noreferrer"
              >
                termos de uso
              </a>{" "}
              e a{" "}
              <a
                href="https://ats.compleo.com.br/PoliticaPrivacidade"
                target="_blank"
                rel="noreferrer"
              >
                política de privacidade
              </a>{" "}
              da COMPLEO.
            </p>
            <SquareLinkButton
              inline
              text="Candidatar-me agora"
              url={url}
              rel="external"
            />
          </div>
        </div>
      </main>
    </>
  );
};

PositionDescription.propTypes = {
  title: string.isRequired,
  date: string.isRequired,
  text: string.isRequired,
  url: string.isRequired,
  id: number.isRequired,
};

export default PositionDescription;
