import React, { useEffect, useState } from "react";
import { bool, func } from "prop-types";

import Config from "../../config";

import MenuLink from "../MenuLink";
import SocialMediaLink from "../SocialMediaLink";
import {
  Instagram,
  Facebook,
  LinkedIn,
  Suitcase,
  Mail,
  User,
  House,
  Plus,
  QuestionCircle,
  Heart,

} from "../../libs/icons";
import InkoinMenuCounter from "../../components/InkoinMenuCounter";

import backgroundImage from "./background.svg";
import styles from "./styles.module.scss";
import style from "../MenuLink/styles.module.scss";


import File from "../../libs/icons/File";

const Menu = ({ visible, handleClose }) => {
  const TIMING = 250;
  const [isVisible, setVisible] = useState(visible);
  const [isActive, setActive] = useState(visible);
  const hasUserToken = localStorage.getItem("@user_token");
  const getMyAccountPath = hasUserToken
    ? "/minha-conta"
    : "/autenticacao/login";


  useEffect(() => {
    if (visible) {
      setVisible(true);
      setTimeout(() => {
        setActive(true);
      }, TIMING);
      return;
    }

    setActive(false);
    setTimeout(() => {
      setVisible(false);
    }, TIMING);
  }, [visible]);

  return (
    <div
      aria-hidden={!visible}
      tabIndex={visible ? "1" : "-1"}
      className={[
        styles.wrapper,
        isActive && styles.isVisible,
        !isVisible && styles.isHidden,
      ].join(" ")}
      style={{
        backgroundImage: `url(${backgroundImage})`,
      }}
    >
      {hasUserToken && <InkoinMenuCounter handleClose={handleClose} />}
      <nav className={styles.navigation}>
        <MenuLink handleClose={handleClose} to="/">
          <House color="#fff" />
          Início
        </MenuLink>
        <MenuLink handleClose={handleClose} to="/area-de-doacoes">
          <Heart color="#fff" /> Área de doações
        </MenuLink>
        <MenuLink handleClose={handleClose} to="/como-funciona">
          <QuestionCircle color="#fff" /> Como funciona
        </MenuLink>
        <MenuLink handleClose={handleClose} to={getMyAccountPath}>
          <User color="#fff" /> Minha conta
        </MenuLink>
        <a className={style.link} href="https://inklua.compleo.com.br/Curriculo">
          <File color="#fff" /> Enviar currículo
        </a>
        <MenuLink handleClose={handleClose} to="/contato">
          <Mail color="#fff" /> Contato
        </MenuLink>
        <span className={styles.split} />
        <MenuLink handleClose={handleClose} to="/para-empresas" blank>
          <Suitcase color="#fff" />
          Para empresas
        </MenuLink>
      </nav>
      <div className={styles.footer}>
        <span className={styles.text}>Siga #inklua</span>
        <SocialMediaLink url={Config.INSTAGRAM_URL}>
          <Instagram color="#fff" />
        </SocialMediaLink>
        <SocialMediaLink url={Config.FACEBOOK_URL}>
          <Facebook color="#fff" />
        </SocialMediaLink>
        <SocialMediaLink url={Config.LINKEDIN_URL}>
          <LinkedIn color="#fff" />
        </SocialMediaLink>
      </div>
      <button className={styles.closeButton} onClick={handleClose}>
        <Plus color="#fff" />
      </button>
    </div>
  );
};

Menu.propTypes = {
  handleClose: func.isRequired,
  visible: bool,
};

Menu.defaultProps = {
  visible: false,
};

export default Menu;
