import { Link } from "react-router-dom";

import ResponsiveImage from "../../components/ResponsiveImage";
import { Inkoin } from "../../libs/icons";
import useAxios from "../../hooks/useAxios";
import { useSnackBarContext } from "../../contexts/SnackBar";
import { useUserContext } from "../../contexts/User";

import BannerImage from "./assets/ilustracao-area-de-doacoes@2x.png";
import BannerImage2x from "./assets/ilustracao-area-de-doacoes@2x.png";
import styles from "./styles.module.scss";

const DonationBanner = () => {
  const isAuthenticated = localStorage.getItem("@user_token");
  const { loading, exec } = useAxios();
  const { showSnackBar } = useSnackBarContext();
  const { updateInkoins } = useUserContext();

  const handleDonate = async () => {
    const response = await exec({
      useCredentials: true,
      method: "POST",
      url: "/actions/donate",
    });

    if (response.message) {
      updateInkoins(0);
      showSnackBar({
        timeout: 2000,
        text: "Inkoins doadas. Obrigado!",
      });

      return;
    }

    showSnackBar({
      timeout: 2000,
      text: "Você não tem saldo para doar",
    });
  };

  return (
    <div className={styles.wrapper}>
      <div className={styles.container}>
        <div className={styles.textWrapper}>
          <h1 className={styles.title}>
            Curso: Programação para <span>pessoas com deficiência</span>
          </h1>
          <p className={styles.description}>
            Doe suas Inkoins e garanta um mercado de trabalho mais{" "}
            <b>#inklusivo</b> para dezenas de pessoas.
          </p>
          {isAuthenticated ? (
            <button
              className={styles.button}
              disabled={loading}
              onClick={handleDonate}
            >
              <Inkoin /> Doar minhas Inkoins
            </button>
          ) : (
            <Link to="/autenticacao/login" className={styles.button}>
              Cadastre-se para doar
            </Link>
          )}
        </div>
        <ResponsiveImage
          className={styles.image}
          alt=""
          images={[BannerImage, BannerImage2x]}
        />
      </div>
    </div>
  );
};

export default DonationBanner;
