import { useEffect } from "react";
import { useSnackBarContext } from "../../contexts/SnackBar";

import styles from "./styles.module.scss";

const SnackBar = () => {
  const { setOpen, isOpen, setText, text } = useSnackBarContext();

  useEffect(() => {
    return () => {
      setOpen(false);
      setText("");
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <p
      className={[styles.alert, isOpen && styles.visible].join(" ")}
      role="alert"
      aria-hidden={!isOpen}
    >
      {text}
    </p>
  );
};

export default SnackBar;
