import React from "react";
import { arrayOf, string } from "prop-types";

import SectionTitle from "../../components/SectionTitle";
import SectionIntro from "../../components/SectionIntro";

import styles from "./styles.module.scss";

const WhereWeAre = ({ locations }) => (
  <section className={styles.wrapper}>
    <SectionIntro level="h2" text="Onde estamos" />
    <SectionTitle level="span" children="Atuamos em <b>todo Brasil</b>" />
    <p className={styles.description}>
      A Inklua está presente em todo território nacional, porém nossos
      escritórios ficam localizados nas cidades abaixo:
    </p>
    <ul className={styles.locations}>
      {locations.map((location, index) => (
        <li key={`location_${index}`}>{location}</li>
      ))}
    </ul>
  </section>
);

WhereWeAre.propTypes = {
  locations: arrayOf(string),
};

WhereWeAre.defaultProps = {
  locations: [
    "São Paulo/SP",
    "Rio de Janeiro/RJ",
    "Belo Horizonte/MG",
    "Jundiaí/SP",
    "São Bernardo do Campo/SP",
  ],
};

export default WhereWeAre;
