import React, { useState, useEffect } from "react";
import qs from "qs";
import { useLocation, useHistory } from "react-router-dom";

import SearchSummary from "../../components/SearchSummary";
import SearchFilter from "../../components/SearchFilter";
import SkeletonList from "../../components/SkeletonList";
import useAxios from "../../hooks/useAxios";
import { Grid, Row, Column } from "../../components/Grid";
import ItemParser from "../../components/ItemParser";
import PaginationWrapper from "../../components/PaginationWrapper";
import { parseCityObjects } from "../../helpers";

const Search = () => {
  const location = useLocation();
  const history = useHistory();
  const query = qs.parse(location.search, {
    ignoreQueryPrefix: true,
    plainObjects: true,
  });

  const [allResults, setAllResults] = useState(query.pagina || 0);
  const [results, setResults] = useState([]);
  const [selectedFilter, setSelectedFilter] = useState(query.cidade || "");
  const [currentPage, setCurrentPage] = useState(1);
  const [filters, setFilters] = useState([]);
  // eslint-disable-next-line no-unused-vars
  const [keyword, setKeyword] = useState(query.termo || null);
  const { exec, loading } = useAxios();

  const shouldFetchCity = (selectedCity) => {
    if (!selectedCity || selectedCity === "Todas") {
      return false;
    }
    return true;
  };

  const fetchData = async () => {
    const response = await exec({
      useCredentials: true,
      method: "get",
      url: `/busca?q=${keyword}&page=${currentPage}${
        shouldFetchCity(selectedFilter) ? `&city=${selectedFilter}` : ""
      }`,
    });

    history.push(
      `/busca?termo=${keyword}${selectedFilter && `&cidade=${selectedFilter}`}`
    );

    setResults([...results, ...response.data]);
    setAllResults(response.total);

    const cities = parseCityObjects(response.cidades);
    setFilters(cities);
  };

  const handleChange = async (event) => {
    const { value } = event.target;

    setResults([]);
    setAllResults(0);
    setCurrentPage(1);
    setSelectedFilter(value);
  };

  const handlePagination = () => {
    setCurrentPage(currentPage + 1);
  };

  useEffect(() => {
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedFilter, currentPage]);

  return (
    <>
      <SearchSummary term={query.termo} quantity={allResults} />
      {loading && <SkeletonList quantity={6} />}
      {Boolean(results.length) && (
        <SearchFilter
          onChange={handleChange}
          selected={selectedFilter}
          options={filters}
        />
      )}
      {Boolean(results.length) && (
        <Grid>
          <Row>
            {results.map((item) => (
              <Column key={`column_${item.id}`}>
                <ItemParser key={`item_${item.id}`} item={item} />
              </Column>
            ))}
          </Row>
          <PaginationWrapper
            onPress={handlePagination}
            placeholderQuantity={6}
          />
        </Grid>
      )}
    </>
  );
};

export default Search;
