import React, { useState } from "react";
import { func, string } from "prop-types";

import { Chevron } from "../../libs/icons";

import styles from "./styles.module.scss";

const ScrollButton = ({ handleClick, defaultColor, hoverColor }) => {
  const [isHovered, setHovered] = useState(false);

  return (
    <button
      data-testid="ScrollButton"
      onClick={handleClick}
      className={styles.scrollButton}
      aria-hidden="true"
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
    >
      <Chevron color={isHovered ? hoverColor : defaultColor} />
    </button>
  );
};

ScrollButton.propTypes = {
  handleClick: func.isRequired,
  defaultColor: string.isRequired,
  hoverColor: string.isRequired,
};

export default ScrollButton;
