import React, { useRef, useState } from "react";
import { arrayOf, string, number, shape } from "prop-types";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import SectionTitle from "../../components/SectionTitle";
import SectionIntro from "../../components/SectionIntro";
import Testimonial from "../../components/Testimonial";
import { Chevron } from "../../libs/icons";

import styles from "./styles.module.scss";

const TestimonialsSection = ({ data }) => {
  const slider = useRef();
  const [currentSlide, setCurrentSlide] = useState(0);

  const handleItemQuantity = () => {
    const max = {
      tablet: 1279,
      mobile: 480,
    };
    const windowWidth = window.innerWidth;

    if (windowWidth <= max.tablet) {
      return Math.floor(window.innerWidth / 304);
    }

    if (windowWidth > max.tablet) {
      const containerWidth = 1092;
      const availableSpace = windowWidth - containerWidth - 32;
      return Math.floor(availableSpace / 304);
    }

    return 1;
  };

  const visibleSlides = handleItemQuantity();

  const handleSlideChange = (current) => {
    setCurrentSlide(current);
  };

  const settings = {
    dots: false,
    infinite: false,
    slidesToShow: visibleSlides,
    speed: 500,
    arrows: false,
    autoPlay: true,
    autoPlaySpeed: 3000,
    variableWidth: window.innerWidth > 640,
    initialSlide: 0,
    centerMode: window.innerWidth <= 640,
    draggable: window.innerWidth <= 640,
    afterChange: handleSlideChange,
  };

  const handleArrowPress = (direction) => {
    if (direction === "prev") {
      slider.current.slickPrev();

      return;
    } else if (direction === "next") {
      slider.current.slickNext();
    }
  };

  return (
    <section className={styles.wrapper}>
      <div className={styles.header}>
        <div>
          <SectionIntro level="span" text="Depoimentos" color="#fff" />
          <SectionTitle
            level="h2"
            children="Ouça a voz de quem
          <br /> passou por nós"
            color="#fff"
          />
        </div>
        <div className={styles.controls}>
          <button
            className={styles.button}
            onClick={() => handleArrowPress("prev")}
            disabled={currentSlide === 0}
          >
            <Chevron color="#fff" />
          </button>
          <button
            className={styles.button}
            onClick={() => handleArrowPress("next")}
            disabled={currentSlide === data.length - visibleSlides}
          >
            <Chevron color="#fff" />
          </button>
        </div>
      </div>
      <div className={styles.slider}>
        <Slider ref={slider} {...settings}>
          {data.map(({ name, quote, description, id }, index) => (
            <div
              key={`testimonial_${id}`}
              className={styles.itemWrapper}
              data-position={index === 0 ? "first" : "not-first"}
            >
              <Testimonial
                quote={quote}
                name={name}
                description={description}
              />
            </div>
          ))}
        </Slider>
      </div>
    </section>
  );
};

TestimonialsSection.propTypes = {
  data: arrayOf(
    shape({
      id: number,
      avatar: string,
      quote: string,
      description: string,
      name: string,
    })
  ).isRequired,
};

export default TestimonialsSection;
