import React, { useState, useRef, useEffect } from "react";
import { useField } from "@unform/core";
import { node, func, string, oneOf } from "prop-types";

import { CrossedEye, OpenEye } from "../../libs/icons";

import warningIcon from "./assets/warning-icon.svg";
import styles from "./styles.module.scss";

const Input = ({
  label,
  name,
  placeholder,
  defaultValue: customDefaultValue,
  type,
  blurCallback,
  icon,
  ...rest
}) => {
  const [showPassword, setShowPassword] = useState(false);
  const inputRef = useRef(null);
  const {
    fieldName,
    defaultValue,
    registerField,
    error,
    clearError,
  } = useField(name);

  const handleType = () => {
    if (type === "password" && showPassword) {
      return "text";
    }

    return type;
  };

  useEffect(() => {
    registerField({
      name: fieldName,
      ref: inputRef.current,
      path: "value",
    });
  }, [fieldName, registerField]);

  return (
    <fieldset className={styles.wrapper}>
      <label htmlFor={`field-${name}`} className={styles.label}>
        {label}
      </label>
      {type === "textarea" && (
        <textarea
          className={[styles.field, error && styles.invalid].join(" ")}
          style={error && { backgroundImage: `url(${warningIcon})` }}
          ref={inputRef}
          id={`field-${name}`}
          placeholder={placeholder}
          onFocus={clearError}
          onBlur={blurCallback}
          aria-invalid={Boolean(error)}
          aria-describedby={`error_${name}`}
          {...rest}
        >
          {defaultValue}
        </textarea>
      )}
      {type !== "textarea" && (
        <input
          className={[styles.field, error && styles.invalid].join(" ")}
          style={error && { backgroundImage: `url(${warningIcon})` }}
          ref={inputRef}
          id={`field-${name}`}
          defaultValue={defaultValue}
          placeholder={placeholder}
          type={handleType()}
          onFocus={clearError}
          autoComplete="one-time-code"
          onBlur={blurCallback}
          aria-invalid={Boolean(error)}
          aria-describedby={`error_${name}`}
          {...rest}
        />
      )}
      {!error && icon && type !== "password" && (
        <span className={styles.iconWrapper} aria-hidden="true">
          {icon}
        </span>
      )}
      {type === "password" && !error && (
        <button
          type="button"
          onClick={() => setShowPassword(!showPassword)}
          className={styles.iconWrapper}
          aria-label={showPassword ? "Hide password" : "Show password"}
          aria-hidden="true"
        >
          {!showPassword ? (
            <CrossedEye color="#8F9BB3" />
          ) : (
            <OpenEye color="#8F9BB3" />
          )}
        </button>
      )}
      {error && (
        <span className={styles.error} id={`error_${name}`}>
          {error}
        </span>
      )}
    </fieldset>
  );
};

Input.propTypes = {
  label: string.isRequired,
  name: string.isRequired,
  placeholder: string,
  type: oneOf(["textarea", "email", "text", "password"]).isRequired,
  blurCallback: func,
  icon: node,
};

Input.defaultProps = {
  placeholder: null,
  blurCallback: () => {},
  icon: null,
};

export default Input;
