import { useState } from "react";
import AccordionItem from "../../components/AccordionItem";
import SectionIntro from "../../components/SectionIntro";
import SectionTitle from "../../components/SectionTitle";
import ResponsiveImage from "../../components/ResponsiveImage";

import Image1x from "../../assets/images/diversidade.jpg";
import Image2x from "../../assets/images/diversidade@2x.jpg";
import Image3x from "../../assets/images/diversidade@3x.jpg";
import styles from "./styles.module.scss";

const AccordionList = () => {
  const [activeTab, setActiveTab] = useState(null);

  const handlePress = (index) => {
    if (index !== activeTab) {
      setActiveTab(index);
      return;
    }

    setActiveTab(null);
  };

  return (
    <section className={styles.wrapper}>
      <div className={styles.leftColumn}>
        <SectionIntro level="span" text="Diversidade" />
        <SectionTitle
          level="h2"
          children="O que fazemos <b>por sua empresa</b>"
        />
        <dl>
          <AccordionItem
            handleClick={() => handlePress(1)}
            active={activeTab === 1}
            title="Recrutamento e Seleção"
            description="
            Entregamos o serviço de recrutamento e seleção de candidatos em
            qualquer lugar do país, de forma ágil e assertiva. Conte com uma
            equipe especializada e dedicada única e exclusivamente à atividade
            de encontrar o profissional certo para a oportunidade que você
            possui."
          />
          <AccordionItem
            handleClick={() => handlePress(2)}
            active={activeTab === 2}
            title="Sensibilização"
            description="
            Inclusão começa com sensibilização! Indicamos profissionais da área
            para que possam entregar o melhor projeto para sua empresa. Fale
            conosco e conectaremos vocês."
          />
          <AccordionItem
            handleClick={() => handlePress(3)}
            active={activeTab === 3}
            title="Mapeamento"
            description="
            Você sabe quais são as oportunidades de inclusão e quais as
            barreiras que possui em sua empresa? Se não, talvez seja o momento
            de mapeá-la para adequar e aumentar sua capacidade de inclusão."
          />
        </dl>
      </div>
      <div className={styles.rightColumn}>
        <ResponsiveImage
          className={styles.image}
          alt=""
          images={[Image1x, Image2x, Image3x]}
        />
      </div>
    </section>
  );
};

export default AccordionList;
