import React, { forwardRef } from "react";

import SectionTitle from "../../components/SectionTitle";
import SectionIntro from "../../components/SectionIntro";

import styles from "./styles.module.scss";

const UnderstandingInklua = forwardRef(({ a }, ref) => (
  <section className={styles.wrapper} ref={ref}>
    <SectionIntro level="h2" text="Como funciona" />
    <SectionTitle
      level="span"
      children="Entenda a <b>Inklua</b>"
      linebreak={false}
    />
    <p className={styles.description}>
      {a}A Inklua busca abrir caminhos e facilitar o acesso ao mercado de
      trabalho para milhares de pessoas com deficiência. Nossa plataforma
      funciona como uma corrente colaborativa composta de elos sociais e
      profissionais que garantem oportunidades para organizações e pessoas no
      Brasil todo.
    </p>
    <p className={styles.description}>
      <b>
        Através de um sistema de recompensas, nossos usuários coletam moedas que
        podem ser doadas para causas voltadas à pessoa com deficiência.
      </b>
    </p>
  </section>
));

export default UnderstandingInklua;
