import React from "react";

import ModuleOne from "../../components/ModuleOne";
import DonationBanner from "../../components/DonationBanner";
import CauseInformation from "../../components/CauseInformation";

import FirstModuleImage from "./assets/causa-1.png";
import FirstModuleImage2x from "./assets/causa-1@2x.png";
import SecondModuleImage from "./assets/causa-2.png";
import SecondModuleImage2x from "./assets/causa-2@2x.png";
import ThirdModuleImage from "./assets/causa-3.png";
import ThirdModuleImage2x from "./assets/causa-3@2x.png";

const CurrentCause = () => {
  return (
    <>
      <DonationBanner />
      <CauseInformation />
      <ModuleOne
        title="Mais do que ouro"
        subtitle="Sobre a <b>causa</b>"
        description="
          <p>Através das suas Inkoins, iremos garantir que pessoas com deficiência do Brasil todo <strong>tenham a oportunidade ganhar uma bolsa integral em um curso de programação</strong>, e assim, dar também o seu primeiro passo dentro do mercado de tecnologia.</p>
        "
        image={[FirstModuleImage, FirstModuleImage2x]}
      />
      <ModuleOne
        title="Abradacabra"
        subtitle="Como irá <b>funcionar?</b>"
        description="<p>As Inkoins resgatadas em sua conta Inklua <strong>poderão ser doadas e convertidas em uma bolsa integral</strong> em um curso remoto de programação.</p>"
        image={[SecondModuleImage, SecondModuleImage2x]}
      />
      <ModuleOne
        title="Cada ação gera uma reação"
        subtitle="Qual o <b>final disso?</b>"
        description="<p>Quando tivermos <strong>Inkoins suficientes em nossa caixinha, abriremos um processo seletivo</strong> aqui em nossa plataforma, onde pessoas com deficiência poderão se candidatar para uma vaga dentro do curso de programação.</p>"
        image={[ThirdModuleImage, ThirdModuleImage2x]}
      />
    </>
  );
};

export default CurrentCause;
