import React from "react";
import { string, number, shape, bool } from "prop-types";

import LocationChip from "../../components/LocationChip";
import SquareLinkButton from "../../components/SquareLinkButton";
import ShareButton from "../../components/ShareButton";
import { toSlug, toDate, parseUrl, parseLocation } from "../../helpers";

import styles from "./styles.module.scss";

const Position = ({
  id,
  image,
  title,
  company,
  date,
  description,
  location,
  shares,
}) => {
  return (
    <article className={styles.wrapper} tabIndex="0">
      <img
        src={image}
        className={styles.image}
        alt={`Imagem da vaga ${title}`}
      />
      <h1 className={styles.title}>{title}</h1>
      <div className={styles.infoWrapper}>
        <span className={styles.company}>{company}</span>
        <span className={styles.date}>
          Publicada em: {toDate(date, "DD/MM/YYYY")}
        </span>
      </div>
      <p className={styles.description}>{description}</p>
      <div className={styles.location}>
        <LocationChip location={parseLocation(location)} />
      </div>
      <div className={styles.footer}>
        <SquareLinkButton
          text="Ver detalhes"
          url={`/vaga/${toSlug(title)}/${id}`}
        />
        <ShareButton
          text="Compartilhar"
          title={title}
          url={parseUrl(`/vaga/${toSlug(title)}/${id}`)}
          id={id}
          shares={shares}
        />
      </div>
    </article>
  );
};

Position.propTypes = {
  id: number.isRequired,
  image: string.isRequired,
  title: string.isRequired,
  company: string.isRequired,
  date: string.isRequired,
  description: string.isRequired,
  location: string.isRequired,
  shares: shape({
    whatsapp: bool.isRequired,
    twitter: bool.isRequired,
    facebook: bool.isRequired,
    linkedin: bool.isRequired,
  }).isRequired,
};

export default Position;
