import React from "react";
import { Helmet } from "react-helmet";

import SectionContactForm from "../../components/SectionContactForm";

import styles from "./styles.module.scss";

const Contact = () => (
  <>
    <Helmet>
      <title>Inklua</title>
      <link rel="canonical" href={window.location.href} />
      <meta
        property="og:description"
        content="Gostaria de conhecer os serviços de Inklua voltados à inclusão de pessoas com deficiência? Ou tem alguma dúvida sobre alguma vaga? Fale conosco"
      ></meta>
      <meta
        property="description"
        content="Gostaria de conhecer os serviços de Inklua voltados à inclusão de pessoas com deficiência? Ou tem alguma dúvida sobre alguma vaga? Fale conosco"
      ></meta>
    </Helmet>
    <div className={styles.wrapper}>
      <SectionContactForm />
    </div>
  </>
);

export default Contact;
