import { useEffect } from "react";
import { useHistory } from "react-router-dom";

import ButtonWithIcon from "../../../../components/ButtonWithIcon";
import useDoubleAuthPrevention from "../../../../hooks/useDoubleAuthPrevention";
import { Exit } from "../../../../libs/icons";
import { useUserContext } from "../../../../contexts/User";

import AuthenticationWrapper from "../../../authentication/components/AuthenticationWrapper";

import WelcomeMessage from "../../components/WelcomeMessage";
import PasswordButton from "../../components/PasswordButton";
import AccountExclusion from "../../components/AccountExclusion";
import ChangeUserInfoForm from "../../components/ChangeUserInfoForm";

const MyAccount = () => {
  const history = useHistory();
  const { validate } = useDoubleAuthPrevention();
  const { userData } = useUserContext();

  const handleLogout = () => {
    localStorage.removeItem("@user_data");
    localStorage.removeItem("@user_token");
    localStorage.removeItem("@user_inkoins");
    localStorage.removeItem("@user_shares");

    history.push("/");
  };

  useEffect(() => {
    validate({ redirect: false });

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <AuthenticationWrapper>
      {userData && (
        <WelcomeMessage email={userData.email} name={userData.name} />
      )}
      <ChangeUserInfoForm />
      <PasswordButton />
      <ButtonWithIcon onClick={handleLogout} text="Sair">
        <Exit />
      </ButtonWithIcon>
      <AccountExclusion />
    </AuthenticationWrapper>
  );
};

export default MyAccount;
