import React, { useRef } from "react";
import { Form } from "@unform/web";
import { Link, useHistory } from "react-router-dom";
import * as Yup from "yup";

import { Input } from "../../../../components/NewInput";
import SquareButton from "../../../../components/SquareButton";
import SquareLinkButton from "../../../../components/SquareLinkButton";
import SectionTitle from "../../../../components/SectionTitle";
import SocialAuthentication from "../../../../components/SocialAuthentication";
import useAxios from "../../../../hooks/useAxios";
import { useUserContext } from "../../../../contexts/User";
import validationDictionary from "../../../../contants/validationDictionary";
import { useSnackBarContext } from "../../../../contexts/SnackBar";

import AuthenticationParagraph from "../../components/AuthenticationParagraph";
import FieldsGrid from "../../components/FieldsGrid";

import styles from "./styles.module.scss";

const SignInForm = () => {
  const formRef = useRef(null);
  const { exec, loading } = useAxios();
  const history = useHistory();
  const { updateInkoins, setUserData } = useUserContext();
  const { showSnackBar } = useSnackBarContext();

  const handleSubmit = async (data) => {
    try {
      const schema = Yup.object().shape({
        email: Yup.string()
          .email(validationDictionary.INVALID_LOGIN)
          .required(validationDictionary.INVALID_LOGIN),
        password: Yup.string()
          .min(6, validationDictionary.INVALID_LOGIN)
          .required(validationDictionary.INVALID_LOGIN),
      });

      await schema.validate(data, {
        abortEarly: false,
      });

      const response = await exec({
        method: "POST",
        url: "/user/login",
        data,
      });

      if (!response.token) {
        formRef.current.setErrors({
          email: response.data.message,
          password: response.data.message,
        });
        return;
      }

      if (response.token && !response.user.accepted_terms) {
        setUserData(response.user);
        updateInkoins(response.user.wallet.coins);
        localStorage.setItem(
          "@user_inkoins",
          JSON.stringify(response.user.wallet.coins)
        );
        localStorage.setItem("@user_token", response.token);
        localStorage.setItem("@user_data", JSON.stringify(response.user));
        history.push("/autenticacao/aceite-os-termos-de-uso");
        return;
      }

      if (response.token && !response.user.email_verified_at) {
        updateInkoins(response.user.wallet.coins);
        localStorage.setItem(
          "@user_inkoins",
          JSON.stringify(response.user.wallet.coins)
        );
        localStorage.setItem("@user_token", response.token);
        localStorage.setItem("@user_data", JSON.stringify(response.user));
        history.push("/autenticacao/confirme-seu-email");
        return;
      }

      updateInkoins(response.user.wallet.coins);
      localStorage.setItem(
        "@user_inkoins",
        JSON.stringify(response.user.wallet.coins)
      );
      localStorage.setItem("@user_token", response.token);
      localStorage.setItem("@user_data", JSON.stringify(response.user));

      formRef.current.reset();
      showSnackBar({
        timeout: 5000,
        text: "Bem-vindo(a) de volta!",
      });
      history.push("/");
    } catch (error) {
      const validationErrors = {};

      if (error instanceof Yup.ValidationError) {
        error.inner.forEach((error) => {
          validationErrors.email = validationDictionary.INVALID_LOGIN;
        });
        formRef.current.setErrors(validationErrors);
      }
    }
  };

  return (
    <Form ref={formRef} onSubmit={handleSubmit} id="contact-form">
      <SectionTitle
        small
        centered
        level="h2"
        children="Que tal se #inkluir <b>nessa com a gente?</b>"
      />
      <AuthenticationParagraph>
        Crie ou acesse sua conta na Inklua
        <br /> para ganhar e doar suas Inkoins.
      </AuthenticationParagraph>
      <SocialAuthentication />
      <FieldsGrid>
        <Input
          label="E-mail"
          name="email"
          placeholder="Digite seu e-mail"
          type="text"
        />
        <Input
          label="Senha"
          name="password"
          placeholder="Digite sua senha"
          type="password"
        />
      </FieldsGrid>
      <Link
        className={styles.forgotPasswordLink}
        to="/autenticacao/esqueci-minha-senha"
      >
        Esqueci minha senha
      </Link>
      <FieldsGrid size="small">
        <SquareButton disabled={loading} text="Entrar" type="submit" />
        <SquareLinkButton
          type="outline"
          text="Criar uma conta"
          url="/autenticacao/cadastro"
        />
      </FieldsGrid>
    </Form>
  );
};

export default SignInForm;
