import React from "react";

import SquareLinkButton from "../SquareLinkButton";

import styles from "./styles.module.scss";

const RetrieveIncoins = () => {
  const url = localStorage.getItem("@user_token") ? "/" : "/autenticacao/login";

  return (
    <section className={styles.wrapper}>
      <h2 className={styles.title}>
        Gostou? Então se <i>#inklua</i>
        <br /> nessa com a gente!
      </h2>
      <SquareLinkButton url={url} text="Quero resgatar Inkoins" />
    </section>
  );
};

export default RetrieveIncoins;
